import {
	login
} from '@/api/api.js'
import {
	access
} from '@/api/request.js'
import {
	mapMutations
} from 'vuex';
import {
	getToken
} from '../../service.js'	
export default {
	components: {},
	data() {
		return {
			account: '',
			password: '',
			url: '',
			//标记是否渲染页面
			isShowPage: false,
		};
	},
	methods: {
		...mapMutations(['login']),
		bindLogin() {
			if (!this.account) {
				uni.showToast({
					icon: 'none',
					title: 'please input the account'
				});
				return;
			}
			if (!this.password) {
				uni.showToast({
					icon: 'none',
					title: 'please input the password'
				});
				return;
			}
			var $this = this
			var apiObj = login(this.account, this.password)
			access(apiObj, function(data) {
				console.log('login', data)
				$this.login(data.data)
				uni.showToast({
					icon: 'none',
					title: data.message
				})
				console.log('redirect_url',$this.url)
				uni.reLaunch({
					url: $this.url
				})
			})
		}
	},
	onLoad(option) {
		console.log('onLoad x',option)
		uni.showLoading({
			title: "loading..."
		})
		if (option.param) {
			this.url = decodeURIComponent(option.param) 
		} else {
			this.url = '/pages/index/index'
		}
		console.log('----------')
		//判断是否登录，是的话直接跳转
		console.log('getToken ----', getToken)
		let token = getToken()
		console.log('token',token)
		if (token) {				
			console.log('有token，直接跳转')
			uni.reLaunch({
				url: this.url
			})
			uni.hideLoading()
			return
		}
		console.log('没有token，保留')
		uni.hideLoading()
		this.isShowPage = true
	},		
}
